/* App.css */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: black;
  z-index: 9000;
  transform: translateY(-100%);
}

.slide-in {
  position: fixed;
  top: 0;
  left: 0;
  width: 20vw;
  height: 100vh;
  background: var(--second-color);
  transform-origin: bottom;
}
.slide-out {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: var(--second-color);
  transform-origin: top;
}
.page-title {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: var(--text-size-700);
  font-weight: 700;
  background: linear-gradient(to right, #fafffb, #d9b08c);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  opacity: 0;
  z-index: 10000;
}
