.container-partial-border {
  position: relative;
  width: 100%;
  border: none;
  text-align: center;
}
.container-partial-border::before,
.container-partial-border::after {
  position: absolute;
  width: 15vw;
  height: 5vh;
  content: "";
}
.container-partial-border::before {
  margin-left: 25px;
  left: 0;
  top: 0;
  border-left: 3px solid var(--second-color);
  border-top: 3px solid var(--second-color);
}
.container-partial-border::after {
  margin-right: 25px;
  right: 0;
  bottom: 0;
  border-right: 3px solid var(--second-color);
  border-bottom: 3px solid var(--second-color);
}
