.grid-container {
  display: grid;
  column-gap: 24px;
  row-gap: 24px;
  width: 100%;
  grid-template-columns: repeat(2, 1fr); /* 2 kolumny o równej szerokości */
}
.grid-template-2 {
  grid-template-areas:
    "text1 image1"
    "image2 text2";
}
.grid-template-3 {
  grid-template-areas:
    "text1 image1"
    "image2 text2"
    "text3 image3";
}
