    height: calc(72px / 2);
    width: 20px;
  }
}
@media only screen and (max-width: 450px) {
  .exp-hero-parent {
    width: auto;
  }
  .logo {
    width: 70%;
    background: var(--second-color);
    height: 48px;
  }
  .logo .nav-link {
    color: var(--main-color) !important;
    font-weight: 600;
  }
  .nav-space {
    width: 20%;
  }
  .nav-links-parent {
    width: 40%;
  }
}
